body {
    font: 14px "Century Gothic", Futura, sans-serif;
    margin: 0px;
    width: 100%;
    height: 100vh;
    background-image: linear-gradient(180deg,#2f6ed3 5%,#5095e4 20%,#5095e4 60%,#2f6ed3 95%);
    background-repeat: no-repeat;
    background-size: cover;
    overflow: hidden;
}

#root
{
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
}

.Juegos
{
    width: 80%;
    height: 80vh;
    background: rgba( 225, 218, 218, 0.5 );
    box-shadow: 0 8px 32px 0 rgba( 31, 38, 135, 0.37 );
    backdrop-filter: blur( 15.5px );
    -webkit-backdrop-filter: blur( 15.5px );
    border-radius: 10px;
    border: 1px solid rgba( 255, 255, 255, 0.18 );
}

.SelectorJuegos
{
    display: flex;
    justify-content: center;

}

.NombreJuegos
{
    width: 100%;
    display: flex;
    justify-content: center
}

.NombreJuegos button
{
    width: 100%;
    padding: 5px;
    background: linear-gradient(180deg, #f0f0f0, #cacaca);
    box-shadow: 0 8px 32px 0 rgba( 31, 38, 135, 0.1 );
    font: 20px "Century Gothic", Futura, sans-serif;
    color: #2f0c50f0;
    border: none;
    border-radius: 0%;
    font-size: 30px;
    cursor: pointer;
}

.NombreJuegos button:hover
{
    background: linear-gradient(180deg, #b09bd1, #b775c7);
}

.NombreJuegos button:active
{
    background: linear-gradient(0deg, #af6cbd, #8d439c);
    color: #e7e6e9f0;
}


.BotonTres
{
    border-radius: 5px 0px 0px 0px !important;
}
.BotonAhorcado
{
    border-radius: 0px 5px 0px 0px !important;
}

.botonSeleccionado
{
    background: #fff0 !important;
    backdrop-filter: none !important;
    box-shadow: none !important;
    border: none !important;
    cursor: default !important;
    pointer-events: none;
}

.JuegoTres
{
    width: 100%;
    height: 75vh;
    display: flex;
    align-content: center;
    justify-content: center;
    align-items: center;
}

.TresEnRaya
{
    width: 100%;
    height: 100%;
    display: flex;
    align-content: center;
    justify-content: center;
    align-items: center;
}

.Ahorcado
{
    width: 100%;
    height: 100%;
    display: flex;
    align-content: center;
    justify-content: center;
    align-items: center;

}

.JuegoAho
{
    width: 100%;
    height: 75vh;
    display: flex;
    align-content: center;
    justify-content: center;
    align-items: center;
}

.oculto
{
    display: none !important;
}


@media only screen and (max-width: 750px) {
    
    .NombreJuegos button
    {
        font-size: 20px;
    }
    
}

@media only screen and (min-width: 1550px) 
{
    .Juegos
    {
        width: 60%;
    }
}