body {
    font: 14px "Century Gothic", Futura, sans-serif;
    margin: 20px;
}

ol,
ul {
    padding-left: 30px;
}

.game
{
    width: 100%;
    height: 100%;
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    align-content: center;
    justify-content: center;
    align-items: center;
}

.game-board
{
    width: 65%;
}

.game-info {
    width: 25%;
    display: flex;
    align-content: space-between;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
    text-align: center;
    
}

.game-status
{
    width: 100%;
    height: 20vh;
    margin-top: 40px;
    font-size: 25px;
    color: #2f0c50f0;
}

.game-button
{
    width: 100%;
}

.game-button button
{
    padding: 30px;
    border: none;
    border-radius: 15px;
    background: linear-gradient(180deg, #f0f0f0, #cacaca);
    font: 14px "Century Gothic", Futura, sans-serif;
    color: #2f0c50f0;
    cursor: pointer;

}

.game-button button:hover
{
    background: linear-gradient(180deg, #b09bd1, #b775c7);
}

.game-button button:active
{
    background: linear-gradient(0deg, #af6cbd, #8d439c);
    color: #e7e6e9f0;
}

.TresEnRaya
{
    margin-top: 50px;
}

.board-row:after {
    clear: both;
    content: "";
    display: table;
}

.status {
    margin-bottom: 10px;
}

.square {
    background: #fff;
    border: 1px solid #999;
    float: left;
    
    font-size:200px;
    margin: 10px;
    font-weight: bold;
    line-height: 34px;
    height: 34px;
    margin-right: -1px;
    margin-top: -1px;
    padding: 0;
    text-align: center;
    width: 180px;
    height: 180px;

    background: rgba( 240, 214, 214, 0.1 );
    box-shadow: 0 8px 32px 0 rgba( 31, 38, 135, 0.37 );
    backdrop-filter: blur( 9px );
    -webkit-backdrop-filter: blur( 9px );
    border-radius: 10px;
    border: 1px solid rgba( 255, 255, 255, 0.18 );
    cursor: pointer;
}

.squareX
{
    color: rgb(65 116 77);
}
.squareO
{
    color: rgb(19 99 206);
}

.square:hover
{
    background: rgba(65, 23, 214, 0.1);
    box-shadow: 0 8px 32px 0 rgba( 31, 38, 135, 0.37 );
    backdrop-filter: blur( 9px );
    -webkit-backdrop-filter: blur( 9px );
    border-radius: 10px;
    border: 1px solid rgba( 255, 255, 255, 0.18 );
}

.square:focus {
    outline: none;
}

.kbd-navigation .square:focus {
    background: #ddd;
}

@media only screen and (max-width: 750px) {
    
    .square
    {
        font-size:60px;
        width: 60px;
        height: 60px;
    }

    .game-board {
        width: 70vw;
        display: flex;
        justify-content: center;
    }

    .game-info
    {
        width: 80%;
    }

    .game-status
    {
        font-size: 25px;
        height: 10vh;

    }

    
}

@media only screen and (min-width: 750px) and (max-width: 1150px) {
    
    .square
    {
        font-size:130px;
        width: 100px;
        height: 100px;
    }
    .game-board {
        width: 65%;
        display: flex;
        justify-content: center;
    }
    .game-status
    {
        font-size: 16px;
    }
}

@media only screen and (min-height: 1000px) {


    .game-board {
        width: 65%;
        display: flex;
        justify-content: center;
    }
}

