body {
    font: 14px "Century Gothic", Futura, sans-serif;
    margin: 20px;
}


.Ahorcado
{
    margin-top: 50px;
}

.game-aho
{
    display: flex;
    flex-wrap: wrap;
}

.game-board-aho
{
    display: flex;
    flex-wrap: wrap;
    width: 100%;
    justify-content: space-around;
    align-content: space-between;
    align-items: center;
}
.game-board-aho div
{
    width: 70%;
    margin: 2%;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}

.palabra
{
    width: 100% !important;
    display: flex;
    text-align: center;
}

.linea
{
    width: 40px;
    height: 50px;
    border-bottom: 1px solid black;
    margin-left: 5px;
}

.letra
{
    font-size: 30px;
}

.botonInicio
{
    padding: 50px;
    border: none;
    border-radius: 15px;
    background: linear-gradient(180deg, #f0f0f0, #cacaca);
    font: 50px "Century Gothic", Futura, sans-serif;
    color: #2f0c50f0;
    cursor: pointer;

}

.botonInicio:hover
{
    background: linear-gradient(180deg, #b09bd1, #b775c7);
}

.botonInicio:active
{
    background: linear-gradient(0deg, #af6cbd, #8d439c);
    color: #e7e6e9f0;
}

.botonLetra
{
    margin-left: 50px;
    padding: 10px;
    border: none;
    border-radius: 15px;
    background: linear-gradient(180deg, #f0f0f0, #cacaca);
    font: 20px "Century Gothic", Futura, sans-serif;
    color: #2f0c50f0;
    cursor: pointer;

}

.botonLetra:hover
{
    background: linear-gradient(180deg, #b09bd1, #b775c7);
}

.botonLetra:active
{
    background: linear-gradient(0deg, #af6cbd, #8d439c);
    color: #e7e6e9f0;
}



.botonRestart
{
    width: 100%;
    margin-left: 30%;
    margin-right: 30%;
    padding: 20px;
    border: none;
    border-radius: 15px;
    background: linear-gradient(180deg, #f0f0f0, #cacaca);
    font: 30px "Century Gothic", Futura, sans-serif;
    color: #2f0c50f0;
    cursor: pointer;

}

.botonRestart:hover
{
    background: linear-gradient(180deg, #b09bd1, #b775c7);
}

.botonRestart:active
{
    background: linear-gradient(0deg, #af6cbd, #8d439c);
    color: #e7e6e9f0;
}

.game-intentosRestantes
{
    display: flex;
    align-content: center;
    justify-content: center;
    align-items: center;
}

.game-intentosRestantes img
{
    width: 300px;
}

.game-aho
{
    width: 100%;
    height: 100%;
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    align-content: flex-start;
    justify-content: center;
    align-items: center;
}

.InputLetra
{
    background: rgba( 225, 218, 218, 0.5 );
    box-shadow: 0 8px 32px 0 rgba( 31, 38, 135, 0.37 );
    backdrop-filter: blur( 15.5px );
    -webkit-backdrop-filter: blur( 15.5px );
    border-radius: 10px;
    border: 1px solid rgba( 255, 255, 255, 0.18 );
    text-align: center;
    font-size: 20px;
}

.game-win
{
    width: 100%;
    font-size: 50px;
    margin-bottom: 50px !important;
}

.ganador
{
    color: #0e5531f0;
}

.perdedor
{
    color: #c10e0ef0;
}

.game-info div
{
    width: 100%;
    font-size: 20px;
    height: 100%;
    margin-top: 50px;
}

@media only screen and (max-width: 600px) {
    
    .linea
    {
        width: 20px;
        height: 30px;
        border-bottom: 1px solid black;
        margin-left: 5px;
    }
    
    .letra
    {
        font-size: 20px;
    }

    .InputLetra 
    {
        width: 30%;
    }

    .botonLetra
    {
        margin-left: 10px;
    }
}

@media only screen and (min-width: 600px) and (max-width: 1000px) {
    
    .linea
    {
        width: 30px;
        height: 40px;
        border-bottom: 1px solid black;
        margin-left: 5px;
    }
    
    .letra
    {
        font-size: 30px;
    }
}

@media only screen and (max-height: 800px) {

    .game-win
    {
        margin-bottom: 0px !important;
        font-size: 35px !important;
    }

}